.dropdown-container {
    position: relative;
    width: 250px;
    margin-top: 20px;
  }
  
  .dropdown-container label {
    display: block;
    margin-bottom: 5px;
  }
  
  .search-input-wrapper {
    position: relative;
    display: flex;
    align-items: center;
  }
  
  .search-input-wrapper input {
    width: 100%;
    padding: 10px 40px 10px 10px;
    border-radius: 25px;
    border: 1px solid #ccc;
    transition: box-shadow 0.3s ease-in-out;
  }
  
  .dropdown-container.focused input {
    box-shadow: 0 0 5px rgba(0, 0, 255, 0.5);
  }
  
  .clear-btn {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    background: none;
    border: none;
    cursor: pointer;
    font-size: 16px;
  }
  
  .dropdown-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
    border: 1px solid #ccc;
    border-radius: 0 0 25px 25px;
    width: 100%;
    max-height: 150px;
    overflow-y: auto;
    position: absolute;
    top: 100%;
    left: 0;
    background: white;
    z-index: 1;
  }
  
  .dropdown-item {
    padding: 10px;
    cursor: pointer;
  }
  
  .dropdown-item:hover {
    background: #f0f0f0;
  }
  