.timepicker-container {
    position: relative;
    width: 250px;
    margin-top: 20px;
  }
  
  .timepicker-container label {
    display: block;
    margin-bottom: 5px;
  }
  
  .timepicker-wrapper {
    position: relative;
    display: flex;
    align-items: center;
  }
  
  .flatpickr-input {
    width: 100%;
    padding: 10px 40px 10px 10px;
    border-radius: 25px;
    border: 1px solid #ccc;
    transition: box-shadow 0.3s ease-in-out;
  }
  
  .timepicker-container.focused .flatpickr-input {
    box-shadow: 0 0 5px rgba(0, 0, 255, 0.5);
  }
  